//stop typekit from flashing fonts
.wf-loading{
  display: none;
}

/*

 */




/*
Titles with links next to them
 */
.link-to-side{
  h2{
    display: inline-block;
  }
  a{
    position: relative;
    top: -2px;
    margin-left: $global-margin/2;
    font-size: $global-font-size*1.25;
  }
}

/*
Social Links
 */
.social{
  a{
    font-size: 1.5em;
    span{
      display: none;
    }
  }
}